export function lifeCycleMixin(Vue) {
    Vue.prototype._c = function () {
        // åˆ›é€ å¯¹åº”çš„è™šæ‹ŸèŠ‚ç‚¹ï¼Œè¿›è¡Œæ¸²æŸ“
        return createElement(this, ...arguments)
    }
    Vue.prototype._v = function () {
        return createTextNode(this, ...arguments)
    }
    Vue.prototype._s = function (value) { //å°†æ•°æ®è½¬åŒ–æˆå­—ç¬¦ä¸²ï¼Œå› ä¸ºä½¿ç”¨å˜é‡å¯¹åº”çš„ç»“æœå¯èƒ½æ˜¯ä¸€ä¸ªå¯¹è±¡
        if (typeof value === 'object' && value !== null) {
            return JSON.stringify(value)
        }
        return value
    }
    Vue.prototype._render = function () {
        const vm = this
        const render = vm.$options.render
        let vnode = render.call(vm)
        return vnode
    }
    Vue.prototype._update = function (vnode) { //å°†è™šæ‹ŸèŠ‚ç‚¹å˜æˆçœŸå®èŠ‚ç‚¹
        // å°†Vnodeæ¸²æŸ“åˆ°elå…ƒç´ ä¸­
        const vm = this
        const el = vm.$el
        const prevVnode = vm._vnode
        vm._vnode = vnode //æŠŠç»„ä»¶ç¬¬ä¸€æ¬¡äº§ç”Ÿçš„è™šæ‹ŸèŠ‚ç‚¹ä¿å­˜åˆ°_vnodeä¸Š
        if (prevVnode) {
            // ä¹‹å‰æ¸²æŸ“è¿‡äº†
            vm.$el = patch(prevVnode, vnode)
        } else {
            vm.$el = patch(el, vnode)
        }
    }
}
