// æ ‡ç­¾å a-aaa
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`;
// å‘½åç©ºé—´æ ‡ç­¾ aa:aa-xxx
const qnameCapture = `((?:${ncname}\\:)?${ncname})`;
// å¼€å§‹æ ‡ç­¾-æ•è·æ ‡ç­¾å
const startTagOpen = new RegExp(`^<${qnameCapture}`);
// ç»“æŸæ ‡ç­¾-åŒ¹é…æ ‡ç­¾ç»“å°¾çš„ </div>
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`);
// åŒ¹é…å±æ€§
const attribute =
    /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/;
// åŒ¹é…æ ‡ç­¾ç»“æŸçš„ >
const startTagClose = /^\s*(\/?)>/;
// åŒ¹é… {{ }} è¡¨è¾¾å¼
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g;
function parseHTML(html) {
    function advance(n) {
        html = html.substring(n);
    }
    let root //æ ‘çš„æ“ä½œï¼Œéœ€è¦æ ¹æ®å¼€å§‹æ ‡ç­¾å’Œç»“æŸæ ‡ç­¾äº§ç”Ÿä¸€ä¸ªæ ‘
    // å¦‚ä½•æ„å»ºæ ‘çš„çˆ¶å­å…³ç³»
    let stack = []
    function creatASTElement(tagName, attrs) {
        return {
            tag: tagName,
            attrs,
            children: [],
            parent: null,
            type: 1
        }
    }
    function start(tagName, attrs) {
        let element = creatASTElement(tagName, attrs);
        if (root == null) {
            root = element
        }
        let parent = stack[stack.length - 1]
        if (parent) {
            element.parent = parent //è®©è¿™ä¸ªå…ƒç´ è®°ä½è‡ªå·±çš„çˆ¶äº²
            parent.children.push(element) // è®©è¿™ä¸ªå…ƒç´ è®°ä½è‡ªå·±çš„å„¿å­
        }
        stack.push(element)
    }
    function end(tagName) {
        stack.pop()
    }
    function chars(text) {
        text = text.replace(/\s/g, '')
        if (text) {
            let parent = stack[stack.length - 1]
            parent.children.push({
                type: 3,
                text
            })
        }
    }
    function parseStartTag() {
        const start = html.match(startTagOpen);
        if (start) {
            const match = {
                tagName: start[1], //æ ‡ç­¾å
                attrs: [],
            };
            advance(start[0].length);
            let end, attr;
            // å¦‚æœä¸æ˜¯å¼€å§‹æ ‡ç­¾çš„ç»“æŸ,é‚£å°±ä¸€ç›´åŒ¹é…
            while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
                match.attrs.push({ name: attr[1], value: attr[3] || attr[4] || attr[5] || true })
                advance(attr[0].length);
            }
            if (end) {
                advance(end[0].length)
                return match
            }
            return false; //ä¸æ˜¯å¼€å§‹æ ‡ç­¾
        }
    }
    while (html) {
        // å¦‚æœtextEndä¸º0,è¯´æ˜æ˜¯ä¸€ä¸ªå¼€å§‹æ ‡ç­¾,æˆ–ä¸€ä¸ªç»“æŸæ ‡ç­¾
        let textEnd = html.indexOf("<"); //å¦‚æœindexOfä¸­çš„ç´¢å¼•æ˜¯0ï¼Œåˆ™è¯´æ˜æ˜¯ä¸ªæ ‡ç­¾
        if (textEnd === 0) {
            const startTagMatch = parseStartTag();
            if (startTagMatch) {
                start(startTagMatch.tagName, startTagMatch.attrs);
                continue
            }
            let matches
            if (matches = html.match(endTag)) {//ä¸æ˜¯å¼€å§‹å°±ä¼šèµ°åˆ°ç»“æŸ
                end(matches[1])
                advance(matches[0].length)
                continue
            }
        }
        let text
        if (textEnd >= 0) {
            text = html.substring(0, textEnd)
        }
        if (text) {
            advance(text.length)
            chars(text)
        }
    }
    return root
}
function getProps(attrs) {
    let str = ''
    for (let i = 0; i < attrs.length; i++) {
        let attr = attrs[i]
        // style = 'color:red;background:blue'
        if (attr.name == 'style') { // style:'{color:red,background:blue}'
            let obj = {}
            attr.value.split(';').reduce((memo, current) => {
                let [key, value] = current.split(':')
                memo[key] = value
                return obj
            }, obj)
            attr.value = obj
        }
        str += `${attr.name}:${JSON.stringify(attr.value)},`
    }
    return `{${str.slice(0, -1)}}`
}
function gen(child) {
    if (child.type === 1) {
        return getCode(child)
    } else {
        let text = child.text
        if (!defaultTagRE.test(text)) {
            return `_v(${JSON.stringify(text)})` //ä¸å¸¦è¡¨è¾¾å¼çš„
        } else {
            let tokens = []
            let match
            //exec é‡åˆ°å…¨å±€åŒ¹é…æœ‰lastIndexé—®é¢˜ï¼Œæ¯æ¬¡åŒ¹é…å‰éœ€è¦å°†lastIndexç½®ä¸º0
            let startIndex = defaultTagRE.lastIndex = 0
            while (match = defaultTagRE.exec(text)) {
                let endIndex = match.index;//åŒ¹é…åˆ°ç´¢å¼•
                if (endIndex > startIndex) {
                    tokens.push(JSON.stringify(text.substring(startIndex, endIndex)))
                }
                tokens.push(`_s(${match[1].trim()})`)
                startIndex = endIndex + match[0].length
            }
            if (startIndex < text.length) { //å°†æœ€åçš„å°¾å·´ä¹Ÿä¸¢è¿›å»
                tokens.push(JSON.stringify(text.slice(startIndex)))
            }
            return `_v(${tokens.join('+')})`
        }
    }
}
function getChildren(children) {
    return children.map(child => gen(child)).join(',')
}
function getCode(ast) {
    // å­—ç¬¦ä¸²æ‹¼æ¥ï¼Œæ‹¼æ¥æˆæƒ³è¦çš„å°±è¡Œ
    let code
    code = `_c('${ast.tag}',${ast.attrs.length ? getProps(ast.attrs) : 'undefined'}${ast.children ? ',' + getChildren(ast.children) : ''})`
    //  _c('div',{className:'xxx'},createTextVnode('hello world'))
    return code
}
// å°†æ¨¡æ¿å˜æˆrenderå‡½æ•°ï¼Œé€šè¿‡with+new Function()çš„æ–¹å¼è®©
export function compileToFunction(template) {
    // å°†templateè½¬åŒ–æˆastè¯­æ³•æ ‘
    let ast = parseHTML(template);
    // é€šè¿‡astè¯­æ³•æ ‘è½¬æˆrenderå‡½æ•°
    let code = getCode(ast)
    // ç”Ÿäº§renderæ–¹æ³•ï¼ˆrenderæ–¹æ³•æ‰§è¡Œåçš„è¿”å›ç»“æœå°±æ˜¯è™šæ‹Ÿdomï¼‰
    const render = new Function(`with(this){return ${code}}`)
    return render
}
