import Dep, { popTarget, pushTarget } from './dep'

let queeu = []
let has = {}
let pending = false //é˜²æŠ–

function flushSchedulerQueue() {
    let flushQueue = queeu.slice(0)
    queeu = []
    has = {}
    pending = false
    flushQueue.forEach(q => q.run()) //åœ¨åˆ·æ–°çš„è¿‡ç¨‹ä¸­å¯èƒ½è¿˜æœ‰æ–°çš„watcherï¼Œé‡æ–°æ”¾åˆ°queueä¸­
}

let callbacks = []
let waiting = false
function flushCallbacks() {
    waiting = false
    let cbs = callbacks.slice(0)
    callbacks = []
    cbs.forEach(cb => cb())
}
// nextTické‡‡ç”¨çš„æ˜¯ä¼˜é›…é™çº§çš„æ–¹å¼
let timeFunc
if (Promise) {
    timeFunc = () => {
        Promise.resolve().then(flushCallbacks)
    }
} else if (MutationObserver) {
    let observer = new MutationObserver(flushCallbacks)
    let textNode = createTextNode(1)
    observer.observe(textNode, {
        characterData: true
    })
    timeFunc = () => {
        textNode.textContent = 2
    }
} else if (setImmediate) {
    timeFunc = () => {
        setImmediate(flushCallbacks)
    }
} else {
    setTimeout(() => {
        flushCallbacks()
    }, 0);
}
export function nextTick(cb) { //å…ˆå†…éƒ¨çš„è¿˜æ˜¯å…ˆç”¨æˆ·çš„ï¼Ÿ
    callbacks.push(cb)
    if (!waiting) {
        timeFunc() //æœ€åä¸€èµ·åˆ·æ–°
    }
}
function queueWatcher(watcher) {
    const id = watcher.id
    if (!has[id]) {
        queeu.push(watcher)
        has[id] = true
        // ä¸ç®¡updateæ‰§è¡Œå¤šå°‘æ¬¡ï¼Œä½†æ˜¯æœ€ç»ˆåªæ‰§è¡Œä¸€æ¬¡
        if (!pending) {
            nextTick(flushSchedulerQueue);
            pending = true
        }
    }
}

let id = 0
class Watcher {
    constructor(vm, exprOrFn, options, cb) { // ä¸åŒç»„ä»¶æœ‰ä¸åŒwatcher
        this.id = id++
        this.renderWatcher = options //æ˜¯ä¸€ä¸ªæ¸²æŸ“è¿‡ç¨‹
        if (typeof exprOrFn === 'string') {
            this.getter = function () {
                return vm[exprOrFn]
            }
        } else {
            this.getter = exprOrFn // getteræ„å‘³ç€è°ƒç”¨è¿™ä¸ªå‡½æ•°ä¼šå‘ç”Ÿå–å€¼æ“ä½œ
        }
        this.deps = [] //åç»­æˆ‘ä»¬å®ç°è®¡ç®—å±æ€§å’Œæ¸…ç†å·¥ä½œä¼šç”¨åˆ°
        this.depsId = new Set()
        this.lazy = options.lazy
        this.dirty = this.lazy //ç¼“å­˜
        this.cb = cb
        this.vm = vm
        this.user = options.user // æ ‡è¯†æ˜¯å¦æ˜¯ç”¨æˆ·è‡ªå·±çš„watcher
        this.value = this.lazy ? undefined : this.get()
    }
    evaluate() {
        this.value = this.get()
        this.dirty = false
    }
    get() {
        pushTarget(this) //é™æ€å±æ€§åªæœ‰ä¸€ä»½
        let value = this.getter.call(this.vm) //ä¼šå»vmä¸Šå–å€¼
        popTarget()
        return value
    }
    addDep(dep) { //ä¸€ä¸ªç»„ä»¶å¯¹åº”å¤šä¸ªå±æ€§ï¼Œé‡å¤çš„å±æ€§ä¹Ÿä¸ç”¨è®°å½•
        let id = dep.id
        if (!this.depsId.has(id)) {
            this.deps.push(dep)
            this.depsId.add(id) //watcherå·²ç»è®°ä½depï¼Œå¹¶ä¸”å·²ç»å»é‡
            dep.addSub(this)
        }
    }
    depend() {
        let i = this.deps.length
        while (i--) {
            this.deps[i].depend() //è®©è®¡ç®—å±æ€§watcherä¹Ÿæ”¶é›†æ¸²æŸ“è¿‡ç¨‹
        }
    }
    update() {
        if (this.lazy) {
            //å¦‚æœæ˜¯è®¡ç®—å±æ€§,ä¾èµ–çš„å€¼å˜åŒ–äº†ï¼Œå°±æ ‡å€¼è®¡ç®—å±æ€§æ˜¯è„å€¼
            this.dirty = true
        } else {
            queueWatcher(this) //æŠŠå½“å‰çš„watcheræš‚å­˜èµ·æ¥
        }
    }
    run() {
        let oldValue = this.value
        let newValue = this.get()
        if (this.user) {
            this.cb.call(this.vm,oldValue, newValue)
        }
    }
}
// éœ€è¦ç»™æ¯ä¸ªå±æ€§å¢åŠ ä¸€ä¸ªdepï¼Œç›®çš„å°±æ˜¯æ”¶é›†watcher
// ä¸€ä¸ªç»„ä»¶ä¸­æœ‰å¤šå°‘å±æ€§ï¼ˆnä¸ªå±æ€§å¯¹åº”ä¸€ä¸ªç»„ä»¶ï¼‰ nä¸ªdepå¯¹åº”ä¸€ä¸ªwatcher
// 1ä¸ªå±æ€§å¯¹åº”å¤šä¸ªç»„ä»¶ 1ä¸ªdepå¯¹åº”å¤šä¸ªwatcher
// å¤šå¯¹å¤šçš„å…³ç³»
export default Watcher